<template>
    <el-dialog title="微信绑定记录" :visible.sync="dialogForm" class="updatePwd">
        <el-table
                :data="tableData"
                stripe
                style="width: 100%"
                v-loading="loading">
            <el-table-column
                    label="操作">
                <template scope="scope">{{ status.wxTypes[scope.row.type]}}</template>
            </el-table-column>
            <el-table-column
                    label="头像">
                <template scope="scope">
                    <img :src="scope.row.avatar" class="user-avatar" width="40" height="40" alt="">
                </template>
            </el-table-column>
            <el-table-column
                    prop="nickname"
                    label="昵称">
            </el-table-column>
            <el-table-column
                    label="性别">
                <template scope="scope">{{ status.wxSex[scope.row.sex]}}</template>
            </el-table-column>
            <el-table-column
                    prop="op_member_id"
                    label="操作人">
            </el-table-column>
            <el-table-column
                    prop="open_id"
                    label="open_id">
            </el-table-column>
            <el-table-column
                    prop="create_time"
                    label="时间">
            </el-table-column>
        </el-table>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogForm = false">关闭</el-button>
        </div>
    </el-dialog>
</template>

<style scope="" lang="less" type="text/scss">
    .updatePwd{
        .el-dialog__header{
            padding:30px;
            .el-dialog__headerbtn{
                width:50px;
                height:50px;
                margin:-30px -30px 0 0;
            }
        }
        .el-dialog__body{
            padding:0 30px;
        }
    }
</style>

<script>
    import QuRequest from '../../../core/QuRequest'

    const quRequest = new QuRequest()

    const _status = {
        wxTypes: {
            '1': '绑定',
            '2': '解绑'
        },
        wxSex: {
            '1': '男',
            '2': '女'
        }
    };
    export default {
        data() {
            return {
                loading: false,
                dialogForm: false,
                tableData: [],
                status: _status,
                form: {
                    member_id: ''
                }
            }
        },
        methods: {
            showDialog(id) {
                if(id){
                    this.form.member_id = id;
                } else {
                    this.form.member_id = '';
                }
                this.dialogForm = true;
                this.getList();
            },
            getList(){
                this.loading = true;
                quRequest.send(quRequest.Apis.qutuiba.member.bindWxLog,this.form
                ).then(res => {
                    if(res.data.length > 0){
                        this.tableData = res.data;
                    }
                    else {
                        this.tableData = [];
                    }
                }).catch(err => {
                    this.$message.error(`获取内容列表数据失败：${ err.message || '未知错误' }`);
                }).fin(() => {
                    this.loading = false;
                }).done();
            }
        }
    }
</script>


